body {
  font-size: 20px;
}
.color_red {
  color: red;
}
.cursor_pointer {
  cursor: pointer;
}
.display_flex_center {
  @include flex(center, center);
  position: relative;
}
.btn_click {
  background-color: blue;
  padding: 5px 15px;
  margin: 20px;
  color: $white;
  &.bg_orange {
    background-color: $primaryColor;
  }
}
.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  .box {
    color: white;
    background-color: red;
    text-align: center;
    width: 250px;
    padding: 15px;
    margin: 10px auto;
    &.ckick_items {
      padding: 50px;
      background-color: $dark;
    }
  }
}
dl {
  width: 30%;
  margin: 0 auto;
  @include borderSetting($borderSolidDark);
  & > dt {
    background-color: blue;
    color: white;
    padding: 5px;
    cursor: pointer;
    & > dd {
      display: none;
      color: black;
      background-color: $white;
      padding: 5px;
    }
  }
}
